<template>
  <div class="card">
    <div class="card-body">
      <div class="row">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="Transactions" key="1">
                <a-table
                  :columns="transactionColumns"
                  :dataSource="transactions"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <span slot="price_usd" slot-scope="text">${{text}}</span>
                  <span slot="price_ngn" slot-scope="text">₦{{text}}</span>
                  <span slot="container_no" slot-scope="text">{{text}}</span>
                  <button @click="deleteTransaction(text.url)" class="btn btn-info" slot="action" slot-scope="text" >{{text.name}}</button>
                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  bestsellersData,
} from './data.json'

const transactionColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Customer Name',
    dataIndex: 'client_name',
    key: 'client_name',
    sorter: (a, b) => a.client_name - b.client_name,
    scopedSlots: { customRender: 'client_name' },
  },
  {
    title: 'Price in USD',
    dataIndex: 'price_usd',
    key: 'price_usd',
    sorter: (a, b) => a.price_usd - b.price_usd,
    scopedSlots: { customRender: 'price_usd' },
  },
  {
    title: 'Price in Naira',
    dataIndex: 'price_ngn',
    key: 'price_ngn',
    sorter: (a, b) => a.price_ngn - b.price_ngn,
    scopedSlots: { customRender: 'price_ngn' },
  },
  {
    title: 'Container Number',
    dataIndex: 'container_no',
    key: 'container_no',
    sorter: (a, b) => a.container_no - b.container_no,
  },
  {
    title: 'Action',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  data: function () {
    return {
      transactionColumns,
      transactions: [],
    }
  },
  mounted() {
    this.getAllTransactions()
  },
  methods: {
    deleteTransaction(id) {
      console.log(id)
      this.axios.post(`transaction/destroy/${id}`)
        .then(response => {
          // console.log(response)
          if (response.status === 200) {
            this.$notification['success']({
              message: 'Transactions Data',
              description: 'Transaction deleted',
            })
            this.getAllTransactions()
          } else {
            this.$notification['warning']({
              message: 'Transactions Data',
              description: 'Sorry we could not delete this transaction',
            })
          }
        })
        .catch(error => {
          // console.log(error)
          this.$notification['warning']({
            message: 'Transactions Data',
            description: 'An error occurred',
          })
          return error
        })
    },
    getAllTransactions() {
      this.axios.get('transaction/all')
        .then(response => {
          // console.log('transactions ', response.data.data.transactions)
          const dt = response.data.data.transactions
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              client_name: dt[i].client_full_name,
              price_usd: dt[i].total_usd,
              price_ngn: dt[i].total_naira,
              container_no: dt[i].container_no,
              id: dt[i].transaction_id,
              key: dt[i].transaction_id,
              action: {
                name: 'Delete',
                url: dt[i].transaction_id,
              },
            }
            data.push(userData)
          }
          this.transactions = data
        })
        .catch(error => {
          // console.log(error)
          return error
        })
    },
  },
}
</script>
